
html,
body {
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100%;
}

.controls {
    position: absolute;
    right: 0;
    top: 0;
    color: #fff;
    padding: 10px;
    /* visibility: hidden; */
    /* opacity: 0; */
}

.color.selected .controls,
.color:hover .controls {
    visibility: visible;
    opacity: 1;
}

.colors {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    padding: 0;
}

/* TODO: avoid important */
.color-dark .controls button {
  color: #fff !important;
}
.color-light .controls button {
    color: #221122 !important;
}

.color-picker {
    padding: 5px;
}

.color-picker-content {
    padding: 0 0 5px;
}

.color-picker-saturation {
    position: relative;
    width: 200px;
    height: 200px;
}

@media (min-width:0px) and (orientation: landscape) {

}

@media (min-width:600px) {
    .color-picker-saturation {
        position: relative;
        width: 300px;
        height: 300px;
    }
    .color-picker {
        padding: 20px;
    }
    
}

.color-picker-saturation {
    margin-bottom: 10px;
}

.color-picker-footer > div {
    -ms-flex: 1 1 100px;
        flex: 1 1 100px;

}

.color-picker-footer .right button {
    float: right;
}

.color-picker-footer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-line-pack: justify;
        align-content: space-between;
    margin: 15px 0;
}


.logo small {
    font-size: 10px;
    vertical-align: top;
    margin: -6px 7px;
    display: inline-block;
    color: #aaa;
}

.logo {
    text-decoration: none;
}

.contnet {
    padding: 60px 0;
    text-align: center;
}


